import React from 'react';
import { Container, Subhead, Text, Box } from 'rebass';

export default ({ title, content, html, children, ...props }) => {
  return (
    <Box {...props} center>
      <Container py={2} center>
        <Subhead center>{title}</Subhead>
        <div style={{textAlign: 'center'}} dangerouslySetInnerHTML={{ __html: html || content }} />
        {children}
      </Container>
    </Box>
  );
};



